

































var page = 1, cate = 0;
export default{
	data() {
		return {
			top : 0,
			//分类信息
			categories : [
				{cateid : 0, name : "全部"},
				{cateid : 1, name : "风景"},
				{cateid : 2, name : "游记"},
				{cateid : 3, name : "世界"}
			],
			// 当前选择的分类
			cateCurrentIndex : 0,
			// 演示文章数据
			artList : []
		};
	},
	onLoad:function(){



		page = 1;
		artList : [];
		this.getNewsList();
	},
	//下拉刷新
	onPullDownRefresh : function(){
		// 重置分页及数据
		page = 1;
		this.artList = [];
		this.getNewsList();
	},
	// 加载更多
	onReachBottom : function(){
		this.getNewsList();
	},
	methods:{
		// 数据和分页是模拟的实际也是这样写
		getNewsList: function(){
			uni.showLoading({});
			// 假设已经到底， 实际根据api接口返回值判断
			if(page >= 3){
				uni.showToast({"title":"已经加载全部", icon:"none"});
				return ;
			}
			uni.request({
				url: 'https://www.easy-mock.com/mock/5bb833775df5622d84ac87ca/example/imgnewlist?page='+page+'#!method=get&cate='+cate,
				method: 'GET',
				data: {},
				success: res => {
					console.log(res);
					var newsList = res.data.data;
					this.artList = this.artList.concat(newsList);
					uni.hideLoading();
					page++;
				},
				complete: res => {
					uni.hideLoading();
					uni.stopPullDownRefresh();
				}
			});
		},
		tabChange : function(e){
			// 选中的索引
			var index  = e.currentTarget.dataset.index;
			// 具体的分类id
			var cateid = e.currentTarget.dataset.cateid;
			this.cateCurrentIndex = index;
			// 动态替换内容
			this.content = this.categories[index].name;
			
			
			// 读取分类数据
			cate = cateid; //把分类信息发送给api接口即可读取对应分类的数据
			// 重置分页及数据 
			page = 1;
			this.artList = [];
			// 加载对应分类数据覆盖上一个分类的展示数据 加载更多是继续使用这个分类
			this.getNewsList();
		}
	}
}
